<template>
  <div class="container my-5">
    <button @click="back" type="button" class="btn-back btn btn-success">
      <i class="bi bi-arrow-return-left"></i>
      Trở về trang trước
    </button>
    <div class="row mt-3">
      <div class="col-6">
        <img
          :src="this.$store.state.detailFood.images[0].imageUrl"
          alt="Sản phẩm"
          class="d-block w-75 mx-auto my-auto"
        />
      </div>
      <div class="col-6">
        <h3 class="heading-food text-center mb-5">
          {{ this.$store.state.detailFood.foodName }}
        </h3>
        <p class="text-justify">
          <b>Mô Tả:</b> {{ this.$store.state.detailFood.description }}
        </p>
        <p class="h4">
          <b>Giá:</b>
          {{ this.$store.state.detailFood.price }}vnđ
        </p>
        <div class="message text-success"></div>
        <p class="text-center mt-2 mb-0">
          <button
            @click="this.addTocart"
            to=""
            class="add-cart d-inline-block w-100"
          >
            <i class="bi bi-cart-plus-fill"></i>
            Thêm vào giỏ hàng
          </button>
        </p>
        
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { mapActions, mapMutations } from "vuex";
export default {
  created: function () {
    var id = this.$route.params.idFood;
    this.getDetailFood(id);
  },
  methods: {
    ...mapMutations(["getListProduct"]),
    ...mapActions(["getDetailFood", "addTocart"]),
    back() {
      return history.back();
    }, async addTocart({commit}) {
    var token = localStorage.getItem("curentUser");
    var idfood = this.$route.params.idFood;
    var msg = document.querySelector(".message")
    if (token != null) {
      const headers = {
        Authorization: "Bearer" + token,
      };
      const body = {
        foodId: idfood,
      };
      await axios
        .post("/order/add-to-cart", body, { headers })
        .then((res) => {
          commit('getListProduct', res.data.data)
        })
        .then( msg.innerHTML = 'Thêm vào giỏ thành công !!!')
    }
  },
  },
};
</script>

<style scoped>
.add-cart {
  padding: 0.1rem 1.5rem;
  margin-right: 1rem;
  font-size: 1.5rem;
  text-decoration: none;
  border: 0.1rem solid #ddd;
  color: rgba(0, 0, 0, 0.8);
}
.add-cart:hover {
  color: #c09b3d;
  background-color: rgba(0, 0, 0, 0.8);
  border: solid 0.1rem #b19140;
  transition: linear 0.2s;
}
</style>